.search-list-input{margin-bottom:10px}.searchable-list{display:-webkit-box;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column}.searchable-list__items{border:1px solid #5b5d66;flex-basis:100%;overflow-y:auto;overflow-x:hidden}.searchable-list__no-item{font-size:.9em;padding:5px 10px;color:#c0c2c6}.searchable-list__item{font-size:.9em;padding:5px 10px;cursor:pointer;border-bottom:1px solid #252b45}.searchable-list__item:hover{background:#3858e7;color:#fff}