.multi-select{display:-webkit-inline-box;display:inline-flex;height:300px}.multi-select__actions{display:-webkit-box;display:flex;-webkit-box-pack:center;justify-content:center;-webkit-box-orient:vertical;-webkit-box-direction:normal;flex-direction:column;-webkit-box-align:center;align-items:center;padding:0 10px}.multi-select__action{cursor:pointer}.multi-select__action:hover{color:#c0c2c6}.multi-select__action-select-all{margin-top:30px}.multi-select__action-unselect-all{margin-top:20px}.multi-select__action-icon{margin-top:17px;color:#919191}.multi-select__list{width:100%}